    // 请求， 然后用请求回来的数据刷新表格，同时显示title和表格数据，然后表格可选，选定的根据point_code，添加进入下一步

    // 卡片在线或者离线，再考虑

    // import {request} from "../../network/request";
    import CardInDTInPM from "./cardindynamictableinpm/CardInDTInPM";
    import axios from 'axios'
    export default {
        name: "DynamicTableInPM",
        data() {
            return {

                // 这部分是用来给drawer表格的
                table: false,
                titleName: '',
                dialog: false,
                loading: false,
                // 这里是drawer表格里的内容,我要从这里拿到写入card的title,和添加进axios请求的数据
                gridData: [],

                // 这个是用来监听开启collapse的index
                activeCollapseName: ['1'],

                //测点值和报警flag，我会把它写入card里在card初始化时加载 TODO：
                arsa_41301: 0,
                prcsa_4201: 0,
                pisa_4204: 0,

                arsa_41301_flag: 0,
                prcsa_4201_flag: 0,
                pisa_4204_flag: 0,

                //用于记录卡片的添加情况,估计会涉及到缓存
                sisCardList: [],
                gasCardList: [],
                dcsCardList: [],
                text: 0,

                //记录哪些被添加
                multipleSelection:[],
            }
        },
        components: {
            CardInDTInPM,
        },
        methods: {

            handleSelectionChange(val) {
                this.multipleSelection = val;
                // eslint-disable-next-line no-console
                console.log(this.multipleSelection)
            },


            isFlagActive(flag) {
              return flag === 1;
            },

            // 用来动态添加卡片的函数
            addCard() {
              this.dcsCardList.push(this.text++);
                // // eslint-disable-next-line no-console
                // console.log(this.dcsCardList);
                // // eslint-disable-next-line no-console
                // console.log(this.text);
            },
          //用来打开drawer前请求当前在线测点数据，传入参数为drawer本身显示参数，测点所属类别
            requestOnlinePointTableForDrawer(categoryName,tableType) {


                this.titleName = categoryName;
                this.table=true;
                this.gridData.length=0;

                axios.post("http://127.0.0.1:8000/online_point_DT/", {Type:tableType})
                    .then(res=>{
                      // 这里我已经拿到列表了，将列表显示在表格上
                        for(let key in res.data) {
                            if(res.data.hasOwnProperty(key)) {
                                this.gridData.push({
                                    // 这个地方要加一个选择
                                    PointID: res.data[key][0],
                                    PointName: res.data[key][1],
                                    PointCode: res.data[key][2],
                                    PointCategory: res.data[key][3],
                                    ConnectionStatus: String(res.data[key][4]),
                                    PointCardIsTab: String(res.data[key][5]),
                                })
                            }
                        }
                    }).catch(err=>{
                    // eslint-disable-next-line no-console
                    console.log(err);
                })
          },
        },
        created() {

//             // 这个地方报错了，应该是吧数组格式的结果赋值给了promise对象产生的，需要解决一下
